import config from '../config/env';

/**
 * Servio para integrao com Replicate Whisper API
 * Suporta processamento assncrono para arquivos grandes sem timeout
 */
class ReplicateService {
  constructor() {
    this.baseUrl = 'https://api.replicate.com/v1';
    this.apiToken = config.replicate.apiToken;
    this.webhookUrl = config.replicate.webhookUrl;
    this.whisperModel = config.transcription.whisperModel;
  }

  /**
   * Verifica se o servio est configurado corretamente
   */
  isConfigured() {
    return !!(this.apiToken && this.webhookUrl && this.whisperModel);
  }

  /**
   * Cria uma predio para transcrio de udio
   * @param {string} audioUrl - URL do arquivo de udio
   * @param {Object} options - Opes adicionais
   * @returns {Promise<Object>} - Dados da predio criada
   */
  async createTranscription(audioUrl, options = {}) {
    if (!this.isConfigured()) {
      throw new Error('Replicate service no est configurado. Verifique as variveis de ambiente.');
    }

    const payload = {
      version: this.whisperModel.split(':')[1], // Extrai o version ID
      input: {
        audio: audioUrl,
        model: 'large-v3',
        translate: options.translate || false,
        language: options.language || null,
        temperature: options.temperature || 0,
        transcription: options.transcription || 'plain text',
        suppress_tokens: options.suppress_tokens || '-1',
        logprob_threshold: options.logprob_threshold || -1.0,
        no_speech_threshold: options.no_speech_threshold || 0.6,
        condition_on_previous_text: options.condition_on_previous_text !== false,
        compression_ratio_threshold: options.compression_ratio_threshold || 2.4,
        temperature_increment_on_fallback: options.temperature_increment_on_fallback || 0.2
      },
      webhook: this.webhookUrl,
      webhook_events_filter: ['start', 'output', 'logs', 'completed']
    };

    try {
      const response = await fetch(`${this.baseUrl}/predictions`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${this.apiToken}`,
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(payload)
      });

      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}));
        throw new Error(`Erro na API Replicate: ${response.status} - ${errorData.detail || response.statusText}`);
      }

      const prediction = await response.json();
      
      // Salvar informaes da predio no localStorage para acompanhamento
      this.savePredictionInfo(prediction.id, {
        id: prediction.id,
        status: prediction.status,
        audioUrl,
        createdAt: new Date().toISOString(),
        model: this.whisperModel,
        options
      });

      return prediction;
    } catch (error) {
      console.error('Erro ao criar transcrio:', error);
      throw error;
    }
  }

  /**
   * Consulta o status de uma predio
   * @param {string} predictionId - ID da predio
   * @returns {Promise<Object>} - Status atual da predio
   */
  async getPredictionStatus(predictionId) {
    if (!this.isConfigured()) {
      throw new Error('Replicate service no est configurado.');
    }

    try {
      const response = await fetch(`${this.baseUrl}/predictions/${predictionId}`, {
        headers: {
          'Authorization': `Bearer ${this.apiToken}`,
          'Content-Type': 'application/json',
        }
      });

      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}));
        throw new Error(`Erro ao consultar predio: ${response.status} - ${errorData.detail || response.statusText}`);
      }

      const prediction = await response.json();
      
      // Atualizar informaes salvas
      this.updatePredictionInfo(predictionId, {
        status: prediction.status,
        output: prediction.output,
        error: prediction.error,
        logs: prediction.logs,
        updatedAt: new Date().toISOString()
      });

      return prediction;
    } catch (error) {
      console.error('Erro ao consultar status da predio:', error);
      throw error;
    }
  }

  /**
   * Cancela uma predio em andamento
   * @param {string} predictionId - ID da predio
   * @returns {Promise<Object>} - Resultado do cancelamento
   */
  async cancelPrediction(predictionId) {
    if (!this.isConfigured()) {
      throw new Error('Replicate service no est configurado.');
    }

    try {
      const response = await fetch(`${this.baseUrl}/predictions/${predictionId}/cancel`, {
        method: 'POST',
        headers: {
          'Authorization': `Bearer ${this.apiToken}`,
          'Content-Type': 'application/json',
        }
      });

      if (!response.ok) {
        const errorData = await response.json().catch(() => ({}));
        throw new Error(`Erro ao cancelar predio: ${response.status} - ${errorData.detail || response.statusText}`);
      }

      const result = await response.json();
      
      // Atualizar status local
      this.updatePredictionInfo(predictionId, {
        status: 'canceled',
        updatedAt: new Date().toISOString()
      });

      return result;
    } catch (error) {
      console.error('Erro ao cancelar predio:', error);
      throw error;
    }
  }

  /**
   * Salva informaes da predio no localStorage
   * @param {string} predictionId - ID da predio
   * @param {Object} info - Informaes da predio
   */
  savePredictionInfo(predictionId, info) {
    try {
      const predictions = this.getAllPredictions();
      predictions[predictionId] = info;
      localStorage.setItem('replicate_predictions', JSON.stringify(predictions));
    } catch (error) {
      console.warn('Erro ao salvar informaes da predio:', error);
    }
  }

  /**
   * Atualiza informaes da predio no localStorage
   * @param {string} predictionId - ID da predio
   * @param {Object} updates - Atualizaes
   */
  updatePredictionInfo(predictionId, updates) {
    try {
      const predictions = this.getAllPredictions();
      if (predictions[predictionId]) {
        predictions[predictionId] = { ...predictions[predictionId], ...updates };
        localStorage.setItem('replicate_predictions', JSON.stringify(predictions));
      }
    } catch (error) {
      console.warn('Erro ao atualizar informaes da predio:', error);
    }
  }

  /**
   * Obtm informaes de uma predio especfica
   * @param {string} predictionId - ID da predio
   * @returns {Object|null} - Informaes da predio
   */
  getPredictionInfo(predictionId) {
    try {
      const predictions = this.getAllPredictions();
      return predictions[predictionId] || null;
    } catch (error) {
      console.warn('Erro ao obter informaes da predio:', error);
      return null;
    }
  }

  /**
   * Obtm todas as predies salvas
   * @returns {Object} - Todas as predies
   */
  getAllPredictions() {
    try {
      const stored = localStorage.getItem('replicate_predictions');
      return stored ? JSON.parse(stored) : {};
    } catch (error) {
      console.warn('Erro ao obter predies:', error);
      return {};
    }
  }

  /**
   * Remove predies antigas (mais de 7 dias)
   */
  cleanupOldPredictions() {
    try {
      const predictions = this.getAllPredictions();
      const sevenDaysAgo = new Date(Date.now() - 7 * 24 * 60 * 60 * 1000);
      
      Object.keys(predictions).forEach(id => {
        const prediction = predictions[id];
        const createdAt = new Date(prediction.createdAt);
        
        if (createdAt < sevenDaysAgo) {
          delete predictions[id];
        }
      });
      
      localStorage.setItem('replicate_predictions', JSON.stringify(predictions));
    } catch (error) {
      console.warn('Erro ao limpar predies antigas:', error);
    }
  }

  /**
   * Obtm predies ativas (em processamento)
   * @returns {Array} - Lista de predies ativas
   */
  getActivePredictions() {
    try {
      const predictions = this.getAllPredictions();
      return Object.values(predictions).filter(p => 
        ['starting', 'processing'].includes(p.status)
      );
    } catch (error) {
      console.warn('Erro ao obter predies ativas:', error);
      return [];
    }
  }
}

// Instncia singleton
const replicateService = new ReplicateService();

// Limpar predies antigas na inicializao
replicateService.cleanupOldPredictions();

export default replicateService;